@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;1,100;1,300;1,400;1,500;1,700&display=swap);
*{box-sizing:border-box;font-family:"Roboto", sans-serif;margin:0;padding:0}:root{--blue: rgb(26, 57, 87);--white: rgb(236, 236, 236)}body.light{--background-color: var(--white);--text-color: var(--blue);--font-weight: 400}body.light .mobile-menu{color:#fff}body.dark{--text-color: var(--white);--font-weight: 500;background-color:rgba(14,14,14,0.9)}body.dark .app{background:rgba(14,14,14,0.9)}body.dark .mobile-menu{color:#fff}body{background:var(--background-color);color:var(--text-color);font-weight:var(--font-weight);transition:background 0.3s ease-in-out, color 0.6s ease-in-out}body .app{min-height:100vh;max-width:1440px;margin:0 auto;position:relative}body .app .background-image-static{position:absolute;top:0;left:0;right:0;bottom:0;z-index:-1;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}body .app .background-image-static img{width:100%;height:100%}

.loading-box{height:100vh;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.loading-box .loading{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;background:transparent;color:#fff;text-align:center;width:100%;height:100%;padding:0;margin:0}.loading-box .loading .dot{background:#0abeff}.loading-box .loading .dot,.loading-box .loading .dot:after{display:inline-block;width:2em;height:2em;border-radius:50%;-webkit-animation:a 2.5s calc(((var(--i) + 0) / var(--n) - 1) * 2.5s) infinite;animation:a 2.5s calc(((var(--i) + 0) / var(--n) - 1) * 2.5s) infinite;-webkit-animation:a 2.5s calc(((var(--i) + var(--o, 0)) / var(--n) - 1) * 2.5s) infinite;animation:a 2.5s calc(((var(--i) + var(--o, 0)) / var(--n) - 1) * 2.5s) infinite}.loading-box .loading .dot:after{--o: 1;background:currentcolor;content:""}@-webkit-keyframes a{0%,50%{-webkit-transform:scale(0);transform:scale(0)}}@keyframes a{0%,50%{-webkit-transform:scale(0);transform:scale(0)}}

